* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App{
  max-width: 1230px;
  min-height: 100vh;
  margin: 0 auto;
  padding: 0 20px;
}
a{
  text-decoration: none;
}
ul li{
  list-style: none;
}
button{
  cursor: pointer;
  border: none;
  outline: none;
  background: transparent;
}

/* HEADER */
@import url("./components/headers/header.css");

/* ADS */
@import url("./components/mainpages/ads/ads.css");

/* AD ITEM */
@import url("./components/mainpages/utils/adItem/adItem.css");

/* CATEGORIES */
@import url("./components/mainpages/categories/categories.css");

/* CREATE ADS */
@import url("./components/mainpages/createAd/createAd.css");

/* LOADING */
@import url("./components/mainpages/utils/loading/loading.css");

/* FOOTER */
@import url("./components/footers/footer.css");

/* RESPONSIVE */
@media (max-width: 820px){
  .App{
    padding: 10px 0;
  }
  .filter_menu{
    flex-direction: column;
  }
}

@media (max-width: 720px){
  .filter_menu {
    width: 100%;
  }
}

@media (max-width: 500px){
  .App{
    padding: 0 10px;
  }
  .detail{
    font-size: 100%;
    padding: 0;
  }
  .filter_menu input{
    margin: 0;
  }
  .filter_menu .sort{
    flex: 1;
  }
  .filter_menu select{
    margin: 10px;
  }
  .filter_menu .sort select{
    width: 100%;
    min-width: 200px;
    margin: 10px;
  }
  .login-page{
    padding: 30px 15px;
  }
  .delete-all{
    margin: 20px 0;
  }
  .create_product .upload{
    margin: 20px 0;
  }
}